import { Injectable } from '@angular/core';
import { Observable, of, throwError } from 'rxjs';
import { IPig, Gender, ICycle } from './pig.model';
import { cycles, pigs } from './mock';

// export interface
@Injectable({
  providedIn: 'root',
})
export class PigsService {
  private pigs: IPig[] = pigs;
  private cycles: ICycle[] = cycles;

  getPigs(): Observable<IPig[]> {
    return of(this.pigs);
  }

  getPig(id: string): Observable<IPig> {
    const pig = this.pigs.find((p) => p.id === id);
    return pig ? of(pig) : throwError('404');
  }

  addPig(pig: IPig): Observable<void> {
    return throwError('');
  }

  getCycles(pigId: string): Observable<ICycle[]> {
    return of(this.cycles.filter((c) => c.pigId === pigId));
  }
}
