import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { filter, map, switchMap } from 'rxjs/operators';
import { PigsService } from '../pigs.service';
import { Observable } from 'rxjs';
import { IPig, ICycle } from '../pig.model';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';

@Component({
  selector: 'app-pig-detail',
  templateUrl: './pig-detail.component.html',
  styleUrls: ['./pig-detail.component.scss']
})
export class PigDetailComponent implements OnInit {
  pig$: Observable<IPig>;
  history$: Observable<ICycle[]>;
  public nbColumns: number = 1;
  public gutter: string = '.8rem';

  constructor(
    private pigService: PigsService,
    private activateRoute: ActivatedRoute,
    private breakpointObserver: BreakpointObserver
  ) {}

  ngOnInit(): void {
    const pigId$ = (this.pig$ = this.activateRoute.params.pipe(
      filter(params => params.id),
      map(params => params.id)
    ));
    this.pig$ = pigId$.pipe(switchMap(id => this.pigService.getPig(id)));
    this.history$ = pigId$.pipe(switchMap(id => this.pigService.getCycles(id)));
    this.breakpointObserver.observe([
      Breakpoints.XSmall,
      Breakpoints.Small,
      Breakpoints.Medium,
      Breakpoints.Large,
      Breakpoints.XLarge
    ]).subscribe(result => {
      if (result.matches) {
        if (result.breakpoints[Breakpoints.XSmall]) {
          this.nbColumns = 1;
        } else if (result.breakpoints[Breakpoints.Small]){
          this.nbColumns = 2;
        } else if (result.breakpoints[Breakpoints.Medium]){
          this.nbColumns = 3;
        } else if (result.breakpoints[Breakpoints.Large]){
          this.nbColumns = 4;
        } else if (result.breakpoints[Breakpoints.XLarge]){
          this.nbColumns = 5;
        }
      }
      console.log(result, Breakpoints.Handset);
    })
  }
}
