import { LayoutModule } from '@angular/cdk/layout';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatButtonModule } from '@angular/material/button';
import { MatCardModule } from '@angular/material/card';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatGridListModule } from '@angular/material/grid-list';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatListModule } from '@angular/material/list';
import { MatSelectModule } from '@angular/material/select';
import { RouterModule, Routes } from '@angular/router';
import { PigDetailComponent } from './detail/pig-detail.component';
import { PigsComponent } from './home/pigs.component';
import { PigListComponent } from './list/pig-list.component';

const routes: Routes = [
  {
    path: '',
    component: PigsComponent,
    children: [
      { path: 'list', component: PigListComponent },
      { path: 'detail/:id', component: PigDetailComponent },
      { path: '', redirectTo: 'list' },
    ],
  },
];

@NgModule({
  declarations: [PigDetailComponent, PigListComponent, PigsComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    MatGridListModule,
    MatListModule,
    MatFormFieldModule,
    MatInputModule,
    MatIconModule,
    MatSelectModule,
    MatCardModule,
    MatButtonModule,
    LayoutModule
  ],
})
export class PigsModule {}
