import { Component, OnInit } from '@angular/core';
import { PigsService } from '../pigs.service';
import { Observable } from 'rxjs';
import { IPig, Gender } from '../pig.model';

@Component({
  selector: 'app-pig-list',
  templateUrl: './pig-list.component.html',
  styleUrls: ['./pig-list.component.scss']
})
export class PigListComponent implements OnInit {
  public pigs$: Observable<IPig[]>;
  public gt: typeof Gender = Gender;

  constructor(private pigsService: PigsService) { }

  public ngOnInit(): void {
    this.pigs$ = this.pigsService.getPigs();
  }
}
